import React, { Component } from 'react'
const contractAddress = "n1ixjKvGGuf4ds4Z9VRxuw6KVayHJeiLfgN"

var Nebulas = require('nebulas')
var Neb = Nebulas.Neb; // Neb
var neb = new Neb(new Nebulas.HttpRequest("https://testnet.nebulas.io"));
var Account = Nebulas.Account;
var api = neb.api;

var NebPay = require("./dist/nebpay");
var nebPay = new NebPay();

let RandomNum=(num)=>{
  let num1=Math.floor(Math.random()*num+1);
  return num1;
}

class CommentInput extends Component {
  constructor () {
    super()
    this.state = {
      num: '',
      content: '',
      whichNum:'???'
    }
  }

  handleNumChange (event) {
    this.setState({
      num: event.target.value
    })
  }

  handleContentChange (event) {
    this.setState({
      content: event.target.value
    })
  }

  handleSubmit () {
    if (this.props.onSubmit) {
      this.props.onSubmit({
        num: this.state.num,
        content: this.state.content,
      })
    }

    this.setState({
      whichNum : RandomNum(this.state.num),
      num: ''})
  }

  getRandomNum()
  {

  }

  render () {
    return (
      <div className='comment-input'>

         <h1>星云链Dapp:我是土豪,我请客 </h1>
         <img src="1.gif" style={{width: '35%'}}/>
            <p>使用方法:请大家记住自己的编号,点击确定键将随机出本次请客的人编号</p>
        <div className='comment-field'>
          <span className='comment-field-name'>用餐人数:</span>
          <div className='comment-field-input'>
            <input
              value={this.state.num}
              onChange={this.handleNumChange.bind(this)} />
          </div>
          <div className='comment-field-button'>
            <button
              onClick={this.handleSubmit.bind(this)}>
              确定
            </button>
          </div>
        </div>
         <h1>恭喜{this.state.whichNum}号请大家吃饭 </h1>
         <p>下面请幸运买单的人输入感言,这将记录在星云链上永不可删除,成为大家永久的回忆</p>
        <div className='comment-field'>

          <span className='comment-field-name'>幸运感言：</span>
          <div className='comment-field-input'>
            <textarea
              value={this.state.content}
              onChange={this.handleContentChange.bind(this)} />
          </div>
        </div>
        <div className='comment-field-button'>
          <button
            onClick={()=>{
              let name = Date()+this.state.content;
              var value = "0";
              var callFunction = "set"
              var callArgs = "[\"" + name + "\"]"
              console.log(callArgs)
              nebPay.call(contractAddress, value, callFunction, callArgs,
              {listener:()=>{alert("记录成功")}});
            }}>
            记录
          </button>
        </div>

      </div>
    )
  }
}

export default CommentInput



